@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    transition: .3s ease;
    
}

a{
    text-decoration: none;

}
 li{
    list-style: none;
 }
 .section{
    padding: 4rem 0 2rem;

}
.container{
    padding-left: 1.5rem;
    padding-right: 1.5rem;

}

.icon{
    font-size: 2rem;
    cursor: pointer;
   

}
.flex{
    display: flex;
    align-items: center;
}
.grid{
    display: grid;
    align-items: center;
}
.btn{
    padding: .4rem 1.5rem;
    background-color: #f8923fc6;
    border: none;
    outline: none;
    border-radius: 3rem;
    cursor: pointer;
}
.btn a{
    color: #fff;
    font-weight: 500;

}
.btn:hover a{

    color: #fff;
}

.secIntro{
    max-width: 70%;
    text-align: center;
    margin: 0 auto 2rem;

}
.secIntro .secTitle{
    color: black;
    line-height: 1.4rem;
    padding-bottom: .5rem;

}
.secIntro p{
    font-size: 13px;
    color: gray;
}
img{
    width: 100%;
    height: auto;

}
input{
    font-size: 100%;
}

/*Media queries*/

@media screen and (min-width: 556px){
    .mainContent{
        grid-template-columns: repeat(2,1fr);
        
    }
    
}

@media screen and (min-width: 884px){
    .mainContent{
        grid-template-columns: repeat(3,1fr);
        
    }
    
}
@media screen and (min-width: 1240px){
    .container{
        width: 75% !important;
        margin: auto !important;
    }

    .section{
        padding: 5rem 0 3rem;
    }
    .secIntro{
        margin: 0 auto 3rem;
    }
    .secIntro , .secTitle{
        line-height: 2rem;
        padding-bottom: 1rem;
        font-size: 2.2rem;

    }
    .secIntro p{
        font-size: 15px;
    }
}

@media screen and(min-width:2560px) {
    body{
        zoom:1.7
    }
    
}

/*page design*/

::selection{
    background: orangered;
    color: white;
}

::-webkit-scrollbar{
    width: 10px;
    background: gray;
}

::-webkit-scrollbar-thumb{
    background: orangered;
    border-radius: 10px;
}

